.container {
  padding-top: 40px;
  display: flex;
  position: fixed;
  left: 0;
  margin-top: 0;
  margin-left: 0;
  width: 100%;
  background-color: #282c34;
  z-index: 1;
}

.container ul {
  padding: 0;
  display: flex;
  width: 100%;
  justify-content: space-around;
  list-style: none;
  padding: 0;
}

.projectsButton {
  background: rgb(0, 175, 117);
  border-radius: 12px;
  border: none;
  padding: 0;
  cursor: pointer;
  outline-offset: 4px;
  width: 274.69px;
}

.buttonFront {
  display: block;
  padding: 12px 42px;
  border-radius: 12px;
  width: 190.69px;
  font-size: 1.25rem;
  font-weight: 600;
  background: rgb(0, 241, 161);
  color: whitesmoke;
  transform: translateY(-6px);
}

.projectsButton:active .buttonFront {
  transform: translateY(-2px);
}

